
// 断链告警页面

<template>
  <d2-container>
    <template slot="header">
      <instence-select :isupf="false" @changeInstence="changeInstance"></instence-select>
    </template>

    <el-table
      v-loading="loading"
      :data="tableData"
      size="small"
      stripe
      highlight-current-row
      style="width: 100%; margin-top: 20px"
    >
      <el-table-column
        type="index"
        :index="indexMethod"
        width="50"
        label="序号"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="crt_time"
        label="时间"
      ></el-table-column>
      <el-table-column align="center" label="断链类型">
        <template slot-scope="scope">
          <p v-if="scope.row.broken_type === 1">n3_gtpu_broken</p>
          <p v-else-if="scope.row.broken_type === 2">n4_gtpu_broken</p>
          <p v-else-if="scope.row.broken_type === 3">"n9_gtpu_broken</p>
          <p v-else-if="scope.row.broken_type === 4">n19_gtpu_broken</p>
          <p v-else-if="scope.row.broken_type === 5">n4_assoc_fail</p>
          <p v-else>"n4_assoc_broken</p>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="broken_detail"
        label="告警内容"
      ></el-table-column>
    </el-table>

    <br />
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalRows"
      :current-page.sync="currentPage"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </d2-container>
</template>

<script>
import {
  fetchGetLinkbroken,
  fetchDelLinkbroken,
} from "@/api/demo/index";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { instenceSelect },
  name: "linkbroken",
  data() {
    return {
      totalRows: 0,
      currentPage: 1,
      serviceName: "",
      instanceValue: "", //当前选中的实例
      timer: null,
      isFirst: true,
      timerInterval: 10000,
      loading: false,
      tableData: [],
      breakType: 1,
      breakTypeList: [
        { name: "n3_gtpu_broken", value: 1 },
        { name: "n4_gtpu_broken", value: 2 },
        { name: "n9_gtpu_broken", value: 3 },
        { name: "n19_gtpu_broken", value: 4 },
        { name: "n4_assoc_fail", value: 5 },
        { name: "n4_assoc_broken", value: 6 },
      ],
    };
  },
  methods: {
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.tableData = [];
      this.instanceValue = val
      // 获取告警数据
      this.getBreakLink();
      // //清除定时并重启
      // if (this.timer) {
      //   clearInterval(this.timer);
      //   this.timer = null;
      //   this.setTimer();
      // } else {
      //   this.setTimer();
      // }
    },
    setTimer() {
      if (this.timer == null) {
        this.timer = setInterval(() => {
          console.log("开始定时...每过一-执行一次");
          this.getBreakLink();
        }, this.timerInterval);
      }
    },
    //分页按钮的触发事件
    handleCurrentChange() {
      this.getBreakLink();
    },
    indexMethod(index) {
      return (this.currentPage - 1) * 10 + index;
    },
    // 获取告警数据
    getBreakLink() {
      this.loading = true;
      fetchGetLinkbroken(
        {
          request_type: "linkage broken",
          instance_id: this.instanceValue,
          sequence_no: 33441,
        },
        this.currentPage
      )
        .then((response) => {
          this.loading = false;
          //刷新图表警告数据
          console.log("linkage broken", response);
          if (response == null) {
            return;
          }

          this.tableData = response.rows;
          this.totalRows = response.total;
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 告警清除
    deleteClick(row) {
      this.$confirm("是否清除数据？", "确认信息", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      })
        .then(() => {
          this.loading = true;
          fetchDelLinkbroken({
            request_type: "linkage broken",
            instance_id: this.instanceValue,
            sequence_no: 33442,
            data: {
              broken_type: row.broken_type,
            },
          })
            .then((response) => {
              this.loading = false;
              this.$message({
                type: "success",
                message: "操作成功",
              });
            })
            .catch((e) => {
              this.loading = false;
              console.log(e);
              this.$alert(e, "网络请求发生错误", {
                confirmButtonText: "确定",
                callback: (action) => {},
              });
            });
        })
        .catch((action) => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
  },
  destroyed: function () {
    // 每次离开当前界面时，清除定时器
    clearInterval(this.timer);
    this.timer = null;
  },
};
</script>
